.robot-state-wrapper {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: space-between;
    align-items: stretch;
    gap: 0.7rem;
    /*  */
    transform: scale(0.97);
    /* transform-origin: top center; */
}

/* .imu-state-block, */
.pos-state-block,
.vel-state-block {
    min-width: 0;
    border-radius: 0.5rem;
    background: rgba(255, 255, 255, 0.03);
    backdrop-filter: blur(3px);

    flex: 1;
    display: flex;
    flex-direction: column;
    min-height: 0;
}

.realtime-pos-state-msg {
    display: flex;
    flex-direction: column;
    gap: 0.16rem;

    color: #adb2c0;
    font-weight: 450;
    font-size: 0.82rem;
}

/* Gauge */
.vel-state-block>div {
    flex: 1;
    min-height: 0;
    display: flex;
    align-items: center;
    justify-content: center;

    /* gap: 0.5rem; */
}

/*  */
.vel-state-block canvas,
.vel-state-block svg {
    transform-origin: top center;
}

/* IMU  */
.imu-state-block {
    display: flex;
    justify-content: center;
    align-items: center;
}

/* IMU  */
.imu-state-block .imu-indicator {
    transform: scale(0.75);
    transform-origin: center;
}

/* Gauge  canvas  */
.gauge-wrapper {
    width: 100%;
    max-width: 280px;
    height: 100%;
    max-height: 220px;

    flex: 0 1 auto;
    /*  */
    aspect-ratio: 1.3 / 1;
}